.header{
    display:  flex;
    justify-content: space-evenly;
    align-items: center;
    border-bottom: 1px solid gray;
    background-color: #FFFFFF;
    position: sticky;
    top: 0px;
    flex-wrap: wrap;
    z-index: 100;
}
.header >img{
    width: 70px;
    object-fit: contain;
    height: auto;
    cursor: pointer;
}

.header_icons{
    display: flex;
    flex-wrap: wrap;
    color: #6C6C6C;
    
}
.header_icons >p{
    font-size: 16px;
    margin: 0px 16px 0px;
    cursor: pointer;
    font-weight: 600;
    padding: 3px 4px;
}
.header_icons >p:hover{
    color: blue;

}