.Aboutus{
    margin-top: 10px;
    font-family: 'PT Sans',sans-serif;
    display: grid;
    /* flex-direction: column; */
    justify-content: center;
}
.testimonial h{
    font-weight: 600;
    margin: 30px;
    padding:30px ;
}
.testimonial_innerText{
    font-family: sans-serif;
    font-weight: 300;
    padding: 30px;
    width:400px;
    background-color: #B5E4F4;
}
.testimonial_arrow{
    width: 20px;
    height: 20px;
    display: inline-block;
    margin: 0 0 0 -10px;
    z-index: 999;
    position: relative; 
    overflow: hidden;
    left: 50%;
    bottom: 11px;
    background-color: #B5E4F4;
    border-width: 0 1px 1px 0;
    border-style: solid;
    border-color: #e0dede;
    -webkit-transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
}

li{
   margin: 5px;

}
.Aboutus span{
    font-family: Oswald;
    font-size: 25px;
    line-height: 35px;
    font-style: normal;
    font-weight: 400;
    text-decoration: none;
    padding: 0px;
    /* margin: 0px; */
    margin-bottom: 20px;
    text-align: center;
}