.footer{
    /* margin-top:px; */
    height: 400px;
    color:white;
    padding: 50px;
    display: flex;
    justify-content: space-around;
    background-color: #232323;
}
.footer div{
    margin-top: 0px;
    display: flex;
    border-left:1px solid #232323;
    flex-direction: column;
    width:20vw;
}
.footer div span{
    padding: 10px;
    color:#8f8f8f;
}
.footer div span:hover{
    font-weight: 700;
    color:white;
    cursor: pointer;
}
.footer div h{
    font-weight: 700;
    margin-bottom: 10px;
}
.footer_search input{
    display: inline-block;
    text-decoration: none; 
    border: 1px solid #e0dede;
     /* border-right: 0;  */
    outline: 0;
    color: #6c6c6c;
    background-color: transparent;
    font-family: "Oswald",sans-serif;
    font-size: 13px;
    margin: 0;
    padding: 2px 4%;
    width: 76%;
    height: 31px;
    line-height: 31px;
     
}